@import url(https://fonts.googleapis.com/css2?family=Rakkas&display=swap);
body {
    font-family: 'Rakkas', cursive !important;
}


.card:hover {
    background-color: #343a40;
    border-color: #343a40;
    transition: .3s;
}
.card {
    transition: .3s;
}

.item-enter {
    opacity: 0;
}
.item-enter-active {
    opacity: 1;
    transition: opacity 1000ms ease-in-out;
}
.item-exit {
    opacity: 1;
}
.item-exit-active {
    opacity: 0;
    transition: opacity 1000ms ease-in-out;
}
